function getNowFormatDate() {
    var date = new Date();
    var month = date.getMonth() + 1;
    var strDate = date.getDate();
    var hours = date.getHours();
    var minutes = date.getMinutes()
    if (month >= 1 && month <= 9) {
        month = '0' + month;
    }
    if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate;
    }
    if (hours <= 9) {
        hours = '0' + hours;
    }
    if (minutes <= 9) {
        minutes = '0' + minutes;
    }
    var day = date.getFullYear().toString() + month + strDate;//20230725
    var time = hours + ':' + minutes;//10:18
    var currentDate = date.getFullYear().toString() + '/' + month + '/' + strDate;//2023/07/25
    // var currentDate = date.getFullYear().toString() + '/' + month + '/' + strDate + ' ' + hours + ':' + minutes;//2023/07/25 10:18
    var curDate = date.getFullYear().toString() + month + strDate + hours + minutes;//202307251018
    return {
        'currentDate': currentDate, 'time': time
    };
}
function getDayName(timeObj) {
    try {
        var td = new Date();
        td = new Date(td.getFullYear(), td.getMonth(), td.getDate());
        var od = new Date(timeObj.currentDate); // '2019-12-21 15:00:02'时间格式不兼容IE和Safari
        od = new Date(od.getFullYear(), od.getMonth(), od.getDate());
        var xc = (od - td) / 1000 / 60 / 60 / 24;
        if (xc === 0) {
            return '今天 ' + timeObj.time;
        } else if (xc >= -1 && xc < 0) {
            return '昨天 ' + timeObj.time;
        } else if (xc >= -2 && xc < -1) {
            return '前天 ' + timeObj.time;
        } else {
            return timeObj.currentDate;
        }
    }
    catch (error) {
        //若用户之前本地存储中缺少对应属性，该函数会报错，导致程序崩溃。即便不做处理catch error也能避免崩溃
    }
}
export { getNowFormatDate, getDayName };

