<template>
  <input type="text" placeholder="回 车 保 存" class="edit" style="display: 'none'"
  :value="targetItem"
  @keyup.enter="$event.target.blur()"
  @blur="outEditing($event.target.value.trim(), targetItem)">
  <!-- keyup.enter触发blur事件，避免一般enter情形下blur事件被enter连带触发！！ -->
</template>

<script>
export default {
  name: 'TodoInput',
  data() {
    return {
    }
  },
  	props: {
    outEditing: Function,
    targetItem: String,
  }
}
</script>

