<template>
  <section class="todoapp">
    <TodoHeader></TodoHeader>
    <TodoMain></TodoMain>
    <TodoFooter></TodoFooter>
  </section>
</template>

<script>
import TodoHeader from './components/TodoHeader.vue'
import TodoMain from './components/TodoMain.vue'
import TodoFooter from './components/TodoFooter.vue'
// 使用vuex中的mapStore
import {mapState} from 'vuex'
// export default {}形式导入的文件，导入的是个obj，以此方式接收并“obj.方法()”使用
// import local from '@/utils/localStorage.js'
// export{}解构形式导出的js文件，函数定义、变量名直接被解构导入，可直接“方法()”调用
// import { saveData, getData } from '@/utils/localStorage.js'

export default {
  name: 'App',
  components: {
    TodoHeader,
    TodoMain,
    TodoFooter,
  },
  data() {
    return {
      // 这里无需定义数据属性，只需在store中定义即可
    }
  },
  created() {
    const todoData = this.$getData();
    if (todoData) {
      // 比较id时间戳，降序 存储
      todoData.dataList.sort((a, b) => b.id - a.id);
      // 若本地有数据todoData， 让this.$store.state的“地址”指向todoData
      this.$store.replaceState(todoData);
    }
    // 关闭页面之前，将假数据 、或state中的数据，保存至localStorage
    window.addEventListener('beforeunload', () => {
      if (!this.dataList.length) {
        // this.$store.state中的dataList没数据时，保存 假数据 至localStorage
        this.$saveData({
          isLoading: false,
          optionCur: 'all',
          dataList: [
            {
              text: '吃点什么呢？？？？',
              _time: { currentDate: "2023/07/23", time: "20:18" },
              status: false,
              id: 0
            },
            {
              text: '人嘛！总得有点追求.....',
              _time: {currentDate: "2023/07/24", time: "10:18"},
              status: false,
              id: 2
            },
            {
              text: '时不我待',
              status: true,
              _time: { currentDate: "2023/07/25", time: "10:18" },
              id: 3
            },
          ]
        });
      } else {
      // 当this.$store.state中的dataList数据 不为空时，将this.$store.state中的最新数据状态 保存至localStorage
      //注意：this.$store.replaceState(todoData);已将this.$store.replaceState的地址重定向到了todoData。
      this.$saveData(todoData);//等同于this.$saveData(this.$store.state);
      }
    });
  },
  computed: {
    ...mapState(['dataList'])
  }
}
</script>

