import Vue from 'vue';
import Vuex from 'vuex';
//timeFormat中定义的方法，须在另一个js文件中使用，要通过import，引入timeFormat.js使用
import { getNowFormatDate } from "@/utils/timeFormat.js";

// 注册Vuex中间件
Vue.use(Vuex);

// 配置Vuex中的Store并导出
export default new Vuex.Store({
    state: {},
    getters: {
        disAllCheck(state) {
            // 列表存在数据时，显示全选按钮
            return state.dataList.length > 0;
        },
        allSumStaus(state) {
            //统计 总条目状态：完成所有条目返回true，否则false(当没数据时，全选按钮 不会被选中)
            return state.dataList.length > 0 && state.dataList.every(ele => ele.status);
        },
        leftSum(state) {
            // 统计 未完成 条目数量(左下角 未完成数量 显示)
            return state.dataList.filter(ele => ele.status == false).length;
        },
        filterDataList(state) {
            // 根据不同选项，过滤数据
            if (state.optionCur == 'active') {
                return state.dataList.filter(ele => !ele.status);
            } else if (state.optionCur == 'completed') {
                return state.dataList.filter(ele => ele.status);
            } else if (state.optionCur == 'all') {
                return state.dataList;
            }
        }
    },
    mutations: {
        addItem(state, val) {
            // 添加条目
            if ((state.dataList.findIndex(ele => ele.text == val) != -1)) {
                alert("本事项 已添加，请勿重复添加！");
                return
            } else {
                let obj = {
                    text: val,
                    status: false,
                    _time: getNowFormatDate(),//已在timeFormat.js中定义，使用前需引入timeFormat.js
                    id: Date.now()
                }
                state.dataList.push(obj);
                // 比较id时间戳，降序 存储
                state.dataList.sort((a, b) => b.id - a.id);
                // 成功添加 待办条目，即切换到active页
                state.optionCur = 'active';
            }
        },
        changeSumStaus(state, status) {
            //  全选框功能：搭配 计算属性allSumStaus，【使 全选 》全不选，部分选或都没选 》全选】
            state.dataList.map(item => item.status = status);
        },
        checkItem(state, id) {
            // 根据点击条目的id，在数组中找到对应条目，更改其状态
            let target = state.dataList.find(ele => ele.id == id);
            if (state.optionCur == 'active') {
                alert("已将其标记为 '已完成'！");
            } else if (state.optionCur == 'completed') {
                alert("已将其标记为 '待办'！");
            }
            target.status = !target.status;
        },
        //mutations中的函数最多接收2个参数。state（必有）和payload（可以是个对象）
        updateItem(state, payload) {
            if ((state.dataList.findIndex(ele => ele.text == payload.val) != -1)) {
                console.log("本事项 已添加！");
                return
            } else {
                if (state.optionCur == 'completed' && confirm("是否将其列为 最新的 待办事项？")) {
                    state.dataList.forEach(ele => {
                        //改变 所有字段，并降序
                        if (ele.text == payload.todoItem) {
                            // store监控的是对象的原地址，不能直接使用对象形式 引用赋值，将无法被store监听到状态改变
                            ele.text = payload.val;
                            ele.status = false;
                            ele._time = getNowFormatDate();
                            ele.id = Date.now()
                        };
                        console.log(ele);
                    });
                    // 比较id时间戳，降序 存储
                    state.dataList.sort((a, b) => b.id - a.id);
                    alert("已将其移至 待办！");
                } else {
                    state.dataList.forEach(ele => {
                        //在原值所在地更改对应text，不改变id、status
                        if (ele.text == payload.todoItem) {
                            ele.text = payload.val;
                        }
                    });
                }
            }
        },
        deleteItem(state, id) {
            // 根据点击条目的id，在数组中找到对应条目，使用其索引删除
            state.dataList.forEach((ele, ind) => {
                if (id == ele.id) {
                    state.dataList.splice(ind, 1);
                }
            });
        },
        clearDone(state) {
            if (state.dataList.length > 0 && confirm("删除 所有 已完成事项？")) {
                // 只保留 未完成 状态 条目
                state.dataList = state.dataList.filter(ele => !ele.status);
            }
        },
        changeLoading(state, status) {
            state.isLoading = status;
        }
    },
    actions: {
        // actions中的函数定义时：有两个参数【context必选、payload可选】，
        //context【是一个包含了 Vuex store 的上下文对象，它包含了一些核心的属性和方法，如 state、getters、commit、dispatch 等，你可以通过解构赋值或直接使用 context 对象来访问这些属性和方法。】
        //payload【为接收的参数，可以是个对象。通过 store.dispatch('actionName', payload) 来传递】
        // 触发异步actions中函数的方式：
        // vue实例methods中：this.$store.dispatch('changeOption');
        // vue实例templete中：$store.dispatch('changeOption');
        // 【dispatch()。参数一，必选！为要触发的 Action 的名称，为字符串。参数二payload，可选，异步操作时传递给 Action函数的数据】
        changeOption({ state, commit }, payload) {
            // 开启loading
            commit("changeLoading", true);
            setTimeout(() => {
                // 更改选项，.2s后切换payload对应选项的数据
                state.optionCur = payload;
                // 关闭loading
                state.isLoading = false;
            }, 200);
        }
    }
});